  width: 12.5rem;
  height: 4.44rem;
  border: 0.06rem solid $ssBlueColor;
  margin: 1rem 0;
  font-size: 1rem;
  &::before{
    content: "";
    position: absolute;
    width: 1rem;
    border-left: 0.06rem solid $ssBlueColor;
    height: 1rem;
    left: 50%;
    top: -1rem;
  }
}
.department-top{
  position: relative;
  width: 12.5rem;
  height: 4.44rem;
  border: 0.06rem solid $ssBlueColor;
  margin: 1rem 0;
